/* Comments */

.comment-content {
  margin-bottom: $space-unit;

  a {
    word-wrap: break-word;
  }
}

.bypostauthor {
  display: block;
}

.comments-area {
  a {
    text-decoration: none;
  }

  h2, h3 {
    margin-bottom: 0;
  }

  h3 {
    font-size: $h2-size;
  }

  label {
    display: block;
  }
}

.comment-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.children {
  list-style: none;
}

.comment-body {
  display: flex;
  padding-top: $space-unit;

  .comment-author-avatar {
    margin-right: $space-unit;
    min-width: 64px;

    img {
      border-radius: 50%;
    }
  }

  .comment-author {
    font-weight: bold;

    cite {
      font-style: normal;
    }
  }

  .comment-metadata {
    color: $light-grey;
  }
}

.comment-list li {
  border-top: $border_solid;
}

.comment-list li:first-child {
  border-top: none;
}

.comment-list {
  border-bottom: $border_solid;
}

.comment-form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  textarea, input {
    width: 100%;
  }

  .comment-form-comment, .comment-form-url {
    flex-basis: 100%;
  }

  .comment-form-author, .comment-form-email {
    flex-basis: 49%;
  }
}

.error {
  color: red;
}

.comment-form-cookies-consent {
  display: flex;
  justify-content: flex-start;
  align-items: center;

  input {
    width: 25px;
  }

  label {
    font-family: $font_family-default;
    text-transform: none;
  }
}